<template>
	<v-card :flat="flat">
		<v-card-title class="info" v-if="title">
			<div class="headline white--text">{{ title }}</div>
			<v-spacer></v-spacer>
		</v-card-title>
		<v-card-text>
			<v-container fluid>
				<v-layout row>
					<v-flex xs4>
						<v-subheader>First name</v-subheader>
					</v-flex>
					<v-flex xs12>
						<v-text-field v-model="data.first_name"></v-text-field>
					</v-flex>
				</v-layout>

				<v-layout row>
					<v-flex xs4>
						<v-subheader>Middle name</v-subheader>
					</v-flex>
					<v-flex xs12>
						<v-text-field v-model="data.middle_name"></v-text-field>
					</v-flex>
				</v-layout>

				<v-layout row>
					<v-flex xs4>
						<v-subheader>Last name</v-subheader>
					</v-flex>
					<v-flex xs12>
						<v-text-field v-model="data.surname"></v-text-field>
					</v-flex>
				</v-layout>

				<v-layout row>
					<v-flex xs4>
						<v-subheader>Extension</v-subheader>
					</v-flex>
					<v-flex xs12>
						<v-text-field v-model="data.name_extension"></v-text-field>
					</v-flex>
				</v-layout>

				<v-layout row>
					<v-flex xs4>
						<v-subheader>Email address</v-subheader>
					</v-flex>
					<v-flex xs12>
						<v-text-field v-model="data.email"></v-text-field>
					</v-flex>
				</v-layout>

				<v-layout row>
					<v-flex xs4>
						<v-subheader>Birthday</v-subheader>
					</v-flex>
					<v-flex xs12>
						<v-text-field
							v-mask="'####-##-##'"
							v-model="data.birthday"
						></v-text-field>
					</v-flex>
				</v-layout>
			</v-container>
		</v-card-text>
	</v-card>
</template>

<script>
export default {
	props: {
		data: Object,
		title: String,
		flat: Boolean,
	},
};
</script>



