import {feathersClient} from '@/services';

const division = {
	namespaced: true,
	state: {},
	getters: {},
	mutations: {},
	actions: {
		async GET_DIVISIONS ({}) {
			try {
				return await feathersClient.service('divisions').find({
					query: {
						$paginate: {
							value: false
						}
					}
				});
			} catch (error) {
				return error;
			}
		}
	}
}

export default division;


